:root {
  --font-family-hauss: "WB Hauss", sans-serif;
}

@supports (font-variation-settings: normal) {
  :root {
    --font-family-hauss: "WB Hauss VF", sans-serif;
  }
}

@font-face {
  font-family: "WB Hauss VF";
  font-display: swap;
  font-weight: 1 850;
  src: url("./fonts/als-hauss-vf.ttf") format("truetype-variations");
}

@font-face {
  font-family: "WB Hauss";
  font-display: swap;
  font-style: normal;
  font-weight: 1;
  src: url("./fonts/hauss-hairline.woff2") format("woff2");
}

@font-face {
  font-family: "WB Hauss";
  font-display: swap;
  font-style: italic;
